
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import { mavonEditor } from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name: "edit",
props: ["id"],
data: function() {
return {
topic: {
title: "",
content: "",

},
toolbars: {
bold: true, // 粗体
italic: true, // 斜体
header: true, // 标题
underline: true, // 下划线
ol: true, // 有序列表
ul: true, // 无序列表
link: true, // 链接
imagelink: true, // 图片链接
code: true, // code
fullscreen: true, // 全屏编辑
htmlcode: true, // 展示html源码
help: true, // 帮助
/* 2.1.8 */
alignleft: true, // 左对齐
aligncenter: true, // 居中
alignright: true, // 右对齐
/* 2.2.1 */
subfield: true, // 单双栏模式
preview: true, // 预览
}
};
},
methods: {

edittopic: function() {
var app = this;
this.$http.post('/topics/update', {
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: 'dev'
}).then(function(response){


app.$router.back();
})
}
},
components: {
Group,
XTextarea,
XButton,
mavonEditor
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http.get("/topic/" + to.params.id).then(function(response) {
vm.topic = response.data.data;
});
});
},
beforeRouteUpdata: function (to, from, next) {
var app = this;
app.$http.get('/topic/' + to.params.id).then(function (response) {
app.topic = response.data.data;
});
next();
}
};



